import React from 'react'
import axios from 'axios'
import 'bootstrap/dist/css/bootstrap.css';
import Pagination from '../pages/components/Pagination'
import {Link}  from 'react-router-dom'
import { Form } from 'react-bootstrap'
import { useState, useEffect} from 'react'


export default function Home() {
const [items, setItems] = useState([])
const [, setisLoading] = useState([])
const [name, setName] = useState([])
const [category, setCategory] = useState([])
const [currentPage, setCurrentPage] = useState(1)
const [namePerPage, setNamePerPage] = useState(20)

// 10 posts per page


 // Fetch from

useEffect(() => {
  const fetchItems = async () => {
  const result = await axios ('https://www.breakingbadapi.com/api/characters')

setItems(result.data)
setisLoading(false)
}


fetchItems()
},[])

// Search input 

const nameSearch = (e) => {setName(e.target.value)}
const categorySearch = (e) => {setCategory(e.target.value)}
const filteredItems = [...items.filter(item => item.name.toLowerCase().includes(name))]  
const secondFilteredItems = [...filteredItems.filter(item => item.category.toLowerCase().includes(category))]

const indexOfLastPost = currentPage * namePerPage
const indexOfFirstPost = indexOfLastPost - namePerPage
const currentPosts = secondFilteredItems.slice(indexOfFirstPost, indexOfLastPost) 


// Paginate
const paginate = (pageNumber) => {setCurrentPage(pageNumber)} 



//Buttons sort

const firstElements= (e) => {
  e.preventDefault()
setNamePerPage(10)
}

const secondElements= (e) => {
  e.preventDefault()
setNamePerPage(20)
}

const thirdElements= (e) => {
  e.preventDefault()
setNamePerPage(40)
}
const [focusColor, setFocusColor] = useState('')

const changeColor= () => {
  
setFocusColor('red')
}


  return (

    // Search Name
    <div className='d-flex justify-content-center flex-column m-5'>
    <form >
      <Form.Group className="d-flex " controlId="formBasicEmail">
    <Form.Control type="text" placeholder="Name"  value={name} onChange={nameSearch} className="w-25 m-2"/>
  </Form.Group>
  <Form.Group className="d-flex " controlId="formBasicEmail">
    <Form.Control type="text" placeholder="Category"  value={category} onChange={categorySearch} className="w-25 m-2"/>
  </Form.Group>

  <Link to='/'><button>Log out</button></Link>
  <div class="btn-group m-2" role="group" aria-label="Basic example">
  <button type="button" className="btn btn-secondary" onClick={firstElements}>10</button>
  <button type="button" className="btn btn-secondary" onClick={secondElements}>20</button>
  <button type="button" className="btn btn-secondary" onClick={thirdElements}>40</button>
</div>
  </form>
    <table class='m-2'>
      <thead>
        <tr>
          <th>Id</th>
          <th>Name</th>
          <th>Birthday</th>
          <th>Status</th>
          <th>Nickname</th>
          <th>Portrayed</th>
          <th>Category</th>
        </tr>
      </thead>
      <tbody>
        { (currentPosts.length > 0) ? currentPosts.map( (currentPost, index) => {
           return (
            <tr key={ index } onClick={changeColor} style={{backgroundColor: focusColor}}>
              <td>{ currentPost.char_id }</td>
              <td>{ currentPost.name }</td>
              <td>{ currentPost.birthday}</td>
              <td>{ currentPost.status }</td>
              <td>{ currentPost.nickname }</td>
              <td>{ currentPost.portrayed }</td>
              <td>{ currentPost.category }</td>
            </tr>
          )
         }) : <tr><td colSpan="5">Loading...</td></tr> }
      </tbody>
    </table>
<Pagination namePerPage={namePerPage} totalPosts={items.length} paginate={paginate}/>
 
    </div>
  )
}
