import 'bootstrap/dist/css/bootstrap.css';
import { Form, Button, Card } from 'react-bootstrap'
import { useState } from 'react'
import {getAuth, createUserWithEmailAndPassword, updateProfile} from 'firebase/auth'
// import { db } from '../firebase.config'
import {Link, useNavigate}  from 'react-router-dom'




export default function SignUp() {

const [formData, setFormData] = useState({
  name: '',
  email: '',
  password: '',
})

const { name , email , password} = formData

const navigate = useNavigate()

const onChange = (e) => {
  setFormData((prevState) => ({
    ...prevState,
    [e.target.id]: e.target.value,
  }))
}

  const onSubmit = async (e) =>{
    e.preventDefault()

    try{
      const auth = getAuth()
      const userCredential = await createUserWithEmailAndPassword (auth, email, password)
      const user = userCredential.user
      
      updateProfile(auth.currentUser,{
        displayName: name,
      })
      navigate('/')
    } catch(error){
      console.log(error)
    }
  }


  
  return (

    <>
      <Card className='form-row align-items-center mt-5'>
      <Card.Body>
        <h2>Sign Up</h2>
        <Form onSubmit={onSubmit}>
          <Form.Group className="mb-3" controlId="formBasicEmail">
            <Form.Label>Enter your name</Form.Label>
              <Form.Control type="name" placeholder="Enter your name" onChange={onChange} id='name' value={name}/>
               
            </Form.Group>
          <Form.Group className="mb-3" controlId="formBasicEmail">
            <Form.Label>Email address</Form.Label>
              <Form.Control type="email" placeholder="Enter email" onChange={onChange} id='email' value={email}/>
                <Form.Text className="text-muted">
                   We'll never share your email with anyone else.
                </Form.Text>
            </Form.Group>

  <Form.Group className="mb-3" controlId="formBasicEmail">
    <Form.Label>Password</Form.Label>
    <Form.Control type="password" placeholder="Enter password" onChange={onChange} id='password' value={password}/>
    <Form.Text className="text-muted">
      We'll never share your email with anyone else.
    </Form.Text>
  </Form.Group>

  
  <Button variant="primary" type="submit" >
    Submit
  </Button>
  
</Form>
<Link to='/sign-in'><p>Already have an account?</p></Link>
      </Card.Body>
      </Card>
    </>
  );
}