import { useState } from 'react';
import 'bootstrap/dist/css/bootstrap.css';
import { Form, Button, Card } from 'react-bootstrap'
import {getAuth, signInWithEmailAndPassword} from 'firebase/auth'
import {Link, useNavigate}  from 'react-router-dom'




export default function SignIn() {

  const [formData, setFormData] = useState({
    email: '',
    password: ''
  })
  
  const { email , password} = formData
  
  const navigate = useNavigate()
  
  const onChange = (e) => {
    setFormData((prevState) => ({
      ...prevState,
      [e.target.id]: e.target.value,
    }))
  }
  
    const onSubmit = async (e) =>{
      e.preventDefault()
  
      try{
        const auth = getAuth()
        const userCredential = await signInWithEmailAndPassword
        (
          auth, 
          email, 
          password,
          )

        if(userCredential.user) {
          navigate('/home-page')
        }
        
        
      } catch(error){
        console.log(error)
      }
    }


  
  return (
    <div>
      <Card className='d-flex align-items-center justify-content-center mt-5' style={{minHeight: '100vh'}}>
      <Card.Body>
        <h2>Sign In</h2>
        <Form onSubmit={onSubmit}> 
          <Form.Group className="mb-3" controlId="formBasicEmail">
            <Form.Label>Email address</Form.Label>
              <Form.Control type="email" placeholder="Enter email"  id={email} onChange={onChange}/>
                <Form.Text className="text-muted">
                   We'll never share your email with anyone else.
                </Form.Text>
            </Form.Group>

  <Form.Group className="mb-3" controlId="formBasicPassword">
    <Form.Label>Password</Form.Label>
    <Form.Control type="password" placeholder="Enter password"  id={password} onChange={onChange}/>
    <Form.Text className="text-muted">
      We'll never share your email with anyone else.
    </Form.Text>
  </Form.Group>

  
  <Button variant="primary" type="submit" >
    Submit
  </Button>

<Link to='/home-page'>
  <Button variant="primary" type="submit" >
    sign in without password
  </Button>
  </Link>
      
     
 
  
</Form>
<Link to='/sign-up'>Dont have an account? Sign Up!</Link>
      </Card.Body>
      </Card>
    </div>
  );
}